


.App-header{
    display: flex;
    flex-direction: column;
    .main-logo{
        color: #a80b0b;
        font-family: "Dancing Script", cursive;
        font-size: 80px;
        text-align: center;
    }
    nav{
        ul{
   
            display: flex;
            justify-content: center;
            li{
                padding: 30px;
                list-style-type: none;
                a:active,
                a:hover, 
                a {
                text-decoration: none; 
                color: #a80b0b;
                }
            }
        }
    }
}

@media(max-width:1200px){
    .cards-man,
    .cards-women{
        grid-template-columns: 1fr 1fr !important;
    }
    .review-container{
        
        grid-template-columns: 1fr 1fr 1fr 1fr !important;
       
    
    }

}

@media(max-width:600px){
    .cards-man,
    .cards-women{
        grid-template-columns: 1fr  !important;
    }
    .review-container{
        
        grid-template-columns: 1fr 1fr !important;
       
    
    }
    nav ul {
       
        flex-direction: column;
        align-items: center;
    }
     nav ul li {
        padding: 5px !important;
       
    }
    .App-main .phone {
        padding: 20px !important;
        text-align: center !important;
        padding-right: 20px !important;
    }
    .company-info{
        p{
            padding: 10px;
        }
    }
    h2{
        font-size: 19px;
        text-align: center;
    }
    .App-main .slider {
        height: 250px !important;
        
    }
    .App-footer{
        flex-direction: column;
        text-align: center;
    }
}

.App-main{
    .phone{
        padding: 20px;
        text-align: right;
        padding-right: 0;
        a{
            text-decoration: none; 
            color: black;
        }
    }
    .slider{
        height: 500px;
       .slide{
           width: 100%;
           height: 100%;
           background-image: url(img/slide-1.png);
           background-position: center;
           background-size: 100% 100%;
       }
    }
    .button-container{
        margin-top: 40px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 10px;
        margin-bottom: 30px;
    }
    .button{
        padding: 16px;
        text-align: center;
        color: white;
        font-size: 20px;
    }
    .button-man{
        background-color: #6cc4b3;
        
    }
    .button-women{
        background-color: #db96ca;
        
    }
    .card-container{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 10px;
        margin-bottom: 30px;
    }
    .cards-man,
    .cards-women{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 10px;
    }
    .cards-man{
        .card{
            background-color: #e9fffb;
        }
    }
    .cards-women{
        .card{
            background-color: #fdebf8;
        }
    }
    .card{
        height: 300px;
        border: 1px solid black;
        
    }
    .review-container{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-gap: 10px;
    
    }
    .review{
        height: 150px;
        background: gainsboro;
    }
    h2{
        margin-bottom: 10px;
    }
    .company-info{
        margin-top: 20px;
        margin-bottom: 20px;
        border-top: 2px solid black;
        border-bottom: 2px solid black;
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .m-b{
        margin-bottom: 10px;
    }
}
.App-footer{
    display: flex;
    margin-top: 40px;
    justify-content: space-between;
    div{
        h3{
            color: #a80b0b;
            font-family: "Dancing Script", cursive;
            font-size: 30px;
        }
    }
    nav{
        ul{
   
            display: flex;
            justify-content: center;
            li{
                padding: 20px;
                list-style-type: none;
                padding-top: 0;
                padding-bottom: 0;
                a:active,
                a:hover, 
                a {
                text-decoration: none; 
                color: black;
                }
            }
        }
    }
    p{
        width: 320px;
    }
}

